* {margin: 0}
.formContainer {
  background-image: url("img/BackGroundLogIn.svg");
  background-color: #A780C5;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper {
    background-color: white;
    padding: 20px 60px;
    border-radius: 42px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;



    .logo {


    }

    span{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      color: #4A484A;
    }
    .title {
      color: #4A484A;

      font-size: 22px;
      font-family: 'Inter';
      font-style: normal;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 45px;

      input {
        font-size: 20px;
        font-family: 'Inter';
        font-style: normal;
        padding: 15px;
        border: 2px solid #FFA4E0;
        border-radius: 30px;
        border-bottom: 1px solid #a7bcff;
        &::placeholder {
          color: rgb(175, 175, 175);
        }
      }

      button {  /* Кнопка регистрации */
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 14px 56px;
        gap: 14px;
        font-size: 25.2px;

        height: 58px;
        font-family: 'Inter';
        font-style: normal;
        color: white;
        background: #A780C5;
        border-radius: 42px;
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: #000000;
        font-size: 22px;
        cursor: pointer;
        font-family: 'Inter';
        font-style: normal;

        img {
          width: 32px;
        }
      }
    }
    p {
      color: #4A484A;
      font-size: 22px;
      margin-top: 10px;
      font-family: 'Inter';
      font-style: normal;
    }
    a{
      font-size: 22px;
      color: #A780C5;
      font-family: 'Inter';
      font-style: normal;
    }
  }
}
