  .sidebar {/*рамка в которой все лежит*/

    width: 25vw;



    background: #EADFF4;
    border-radius: 30px;


    .search { /*рамка поиска */
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin: 15px 10px 5px 10px;

      box-sizing: border-box;


      width: 89%;
      height: 7%;


      border: 2px solid #A780C5;
      border-radius: 30px;

      .searchForm {

        display: flex;
        justify-content: center;

        border: none;



        input { /* cтрока ввода (сам текст)*/
          background-color: #EADFF4;
          outline: none;
          border: 0;
          font-family: 'Inter';
          font-style: normal;
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          width: 80%;
          color: #A780C5;


          &::placeholder {
            color: lightgray;
          }

        }
      }
    }

    .userChat {
      padding: 10px;
      display: flex;
      gap: 10px;
      color: white;
      cursor: pointer;


      &:hover {
        background-color: white;
        border-radius: 23px;
      }

      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
      }

      .userChatInfo {

        span {
          color: #333232;
          font-size: 18px;
          font-weight: 500;
          font-family: 'Inter';
          font-style: normal;
        }
        p {
          font-size: 14px;

          color: #1E1E1E;
          font-family: 'Inter';
          font-style: normal;
        }
      }
      .userChatTime{
        span {
          color: rgba(74, 74, 74, 0.84);
          font-size: 18px;
          font-weight: 500;
          font-family: 'Inter';
          font-style: normal;
        }
        p {
          font-size: 14px;
          color: white;
          background-color: #A780C5;
          font-family: 'Inter';
          font-style: normal;


          overflow: hidden;
        }
      }
    }
  }




    .chat {
      flex-grow: 1;




      border: 1px solid #EADFF4;





      .chatInfo {

        flex-grow: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: start;


        height: 60px;
        left: 498px;
        top: 8px;

        background: linear-gradient(257.23deg, #F89E9D 4.07%, #BF93CB 53.87%);
        border-radius: 20px;
        .personalImage{
          display:flex;
          align-items:center;
          justify-content: center;

          margin-left: 40px;


          img {

            width: 40px;
            height: auto;
            border-radius: 50%;

          }
        }
        .nameOfPerson{
          margin-left: 20px;
          .pName{
            color: #FFFFFF;
            font-family: 'Inter';
            font-style: normal;
            font-weight:bold;
            line-height: 17px;
            font-family: 'Inter';
            font-style: normal;
          }
          .pTimeOnline{
            color: rgba(255, 230, 245, 0.75);
            font-family: 'Inter';
            font-style: normal;
          }
        }


      }

      .chatIcons {
        display: flex;
        gap: 10px;

        img {
          height: 24px;
          cursor: pointer;
        }
      }

      .messages {
        background: url("img/ChatBackground.svg");
        border-radius: 20px;
        padding: 10px;
        height: calc(100% - 160px);
        overflow-y: auto;


        .message {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;

          .messageInfo {
            display: flex;
            flex-direction: column;
            color: white;
            font-weight: 300;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
            }
          }
          .messageContent {
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;



            p {
              Background-color: #E48B9E;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              max-width: max-content;
              color: white;
              font-family: 'Inter';
              font-style: normal;
            }

            img {
              width: 50%;
            }
          }

          &.owner {
            flex-direction: row-reverse;

            .messageContent {
              align-items: flex-end;
              p {
                background-color: #A780C5;
                color: white;
                border-radius: 10px 0px 10px 10px;
              }
            }
          }
        }
      }

      .input {
        height: 50px;
        background-color: white;
        border-radius: 20px;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 2px solid #D0AAF8;
        border-radius: 30px;
        font-family: 'Inter';
        font-style: normal;
        font-size: 20px;

        input {
          width: 100%;
          border: none;
          outline: none;
          color: #2f2d52;
          font-size: 20px;
          font-family: 'Inter';
          font-style: normal;


          &::placeholder {
            color: lightgray;
          }
        }

        .send {
          display: flex;
          align-items: center;
          gap: 10px;

          img {
            height: 24px;
            cursor: pointer;
          }

          button {
            border: none;
            border-radius: 50%;
            padding: 10px 15px;

            background-color: #8da4f1;
            cursor: pointer;
            &:hover {
              background-color: #E48B9E;

            }
            .imgCatPaw{

            }

